@font-face {
  font-family: "Montserrat";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
body {
  margin: 0;
}
img {
  height: auto;
  max-width: 100%;
}
a {
  text-decoration: none;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-main {
  overflow-x: hidden;
}
.container {
  padding: 0 16px;
  margin: 0 auto;
  max-width: 320px;
}
.container--mob-100 {
  padding: 0;
  max-width: 768px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 21px;
    max-width: 768px;
  }
}
@media (min-width: 1230px) {
  .container {
    padding: 0 30px;
    max-width: 1230px;
  }
}
@media (min-width: 1230px) {
  .container--desktop {
    padding: 0 30px;
    max-width: 1230px;
  }
}
.page {
  height: 100%;
  font-family: "Montserrat", "Tahoma", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 28px;
  scroll-behavior: smooth;
}
.page__body {
  min-height: 100%;
  margin: 0;
}
.page-header {
  position: relative;
  padding-top: 15px;
  padding-bottom: 18px;
}
@media (min-width: 1230px) {
  .page-header {
    display: grid;
    grid-template-columns: 70px 1fr;
  }
}
.page-header__nav-wrapper {
  display: flex;
  justify-content: flex-end;
}
.page-header__logo {
  padding-left: 16px;
  width: 64px;
  height: 17px;
}
@media (min-width: 768px) {
  .page-header__logo {
    padding-left: 0;
  }
}
@media (min-width: 1230px) {
  .page-header__logo {
    width: 67px;
    height: 22px;
  }
}
.page-header__toggle {
  position: absolute;
  top: 0;
  right: 0;
  width: 50px;
  height: 50px;
  background-color: #fe7865;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .page-header__toggle {
    width: 72px;
  }
}
@media (min-width: 1229px) {
  .page-header__toggle {
    display: none;
  }
}
.page-header__toggle::before {
  content: "";
  position: absolute;
  top: 22px;
  right: 13px;
  width: 22px;
  height: 2px;
  background-color: #dadada;
  box-shadow: 0 8px 0 0 #dadada, 0 -8px 0 0 #dadada;
}
@media (min-width: 768px) {
  .page-header__toggle::before {
    right: 26px;
  }
}
.page-header__phone {
  position: relative;
  display: none;
}
.page-header__phone::before {
  content: "";
  position: absolute;
  left: -40px;
  width: 30px;
  height: 30px;
  background-image: url("../img/svg/phone.svg");
  background-repeat: no-repeat;
}
@media (min-width: 1230px) {
  .page-header__phone {
    display: block;
  }
}
.main-nav {
  position: relative;
  top: -35px;
  right: 0;
  z-index: 3;
}
@media (min-width: 1230px) {
  .main-nav {
    position: relative;
    top: 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
}
.main-nav__toggle {
  position: absolute;
  top: 7px;
  right: 0;
  width: 50px;
  height: 50px;
  background-color: transparent;
  cursor: pointer;
  border: none;
  z-index: 3;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    right: -20px;
  }
}
@media (min-width: 1230px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle::before,
.main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 18px;
  right: 13px;
  width: 32px;
  height: 1px;
  background-color: rgba(20, 21, 23, 0.2);
  z-index: 5;
}
.main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav__toggle::after {
  transform: rotate(-45deg);
}
@media (max-width: 1229px) {
  .main-nav--closed .main-nav__wrapper,
  .main-nav--closed .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 1230px) {
  .main-nav--closed .main-nav__wrapper {
    display: block;
  }
}
.main-nav--nojs .main-nav__wrapper {
  position: relative;
  display: block;
  background-color: #ffffff;
  width: 320px;
  min-height: 568px;
  right: -21px;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
@media (max-width: 1229px) {
  .main-nav--nojs .main-nav__list {
    display: block;
  }
}
@media (max-width: 1229px) {
  .main-nav__wrapper--show {
    position: absolute;
    background-color: #ffffff;
    width: 320px;
    min-height: 568px;
    margin-left: auto;
    right: -21px;
  }
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__site-list {
  padding-top: 134px;
}
@media (min-width: 1230px) {
  .main-nav__site-list {
    padding-top: 0;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-right: 30px;
  }
}
.main-nav__item {
  position: relative;
  padding-top: 17px;
  padding-right: 35px;
  padding-bottom: 17px;
  padding-left: 16px;
  margin-bottom: 30px;
  text-align: left;
  background-color: #fbf8f8;
}
.main-nav__item::after {
  content: "";
  position: absolute;
  top: 25px;
  right: 24px;
  width: 12px;
  height: 12px;
  background-image: url("../img/svg/arrow-menu.svg");
  background-repeat: no-repeat;
}
@media (min-width: 1230px) {
  .main-nav__item {
    padding: 0;
    margin: 0;
    background-color: transparent;
    margin-right: 40px;
  }
  .main-nav__item::after {
    display: none;
  }
}
.main-nav__item a {
  font-size: 20px;
  font-weight: 600;
  line-height: 28px;
  color: #48494d;
  display: block;
  text-decoration: none;
}
@media (min-width: 1230px) {
  .main-nav__item a {
    font-size: 14px;
    font-weight: 500;
    line-height: 17px;
    color: #141517;
  }
}
.main-nav__item a:hover[href] {
  color: #fe7865;
}
.main-nav__item a:focus[href] {
  color: #fe7865;
}
.main-nav__item a:active[href] {
  color: #fe7865;
}
